@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PT_Mono.woff2") format("woff2"), url("../fonts/PT_Mono.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

html {
  scroll-behavior: smooth;
}

body {
  display: flex;
  flex-direction: column;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  position: sticky;
  top: 0;
  z-index: 4;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 28px 50px 36px;
  background: linear-gradient(180deg, rgb(4, 24, 39) 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .header {
    position: static;
    display: block;
    padding: 20px 80px 25px 50px;
  }
}
@media (max-width: 769px) {
  .header {
    padding: 15px 45px 20px 25px;
  }
}

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 150px;
  margin-right: 60px;
}
@media (max-width: 1340px) {
  .header__wrapper {
    gap: 15px;
    margin-right: 0;
  }
}
@media (max-width: 1023px) {
  .header__wrapper {
    align-items: initial;
    margin-right: 0;
    gap: 0;
  }
}

.header__logo {
  margin-right: 10px;
}
@media (max-width: 1023px) {
  .header__logo {
    margin-right: auto;
  }
}

.header__logo:hover,
.header__logo:focus {
  outline: none;
  box-shadow: 0 0 8px rgb(0, 178, 255);
}

.header__logo:active {
  outline: none;
  box-shadow: 0 0 8px rgb(0, 178, 255);
}

@media (max-width: 769px) {
  .header__logo-image {
    width: 35px;
    height: 35px;
  }
}

.header__nav {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .header__nav {
    margin-top: 10px;
  }
}
@media (max-width: 769px) {
  .header__nav {
    margin-top: 6px;
  }
}

.header__link {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-right: 120px;
}
@media (max-width: 1023px) {
  .header__link {
    margin-right: 39px;
    align-items: flex-start;
  }
}
.header__link p {
  max-width: 200px;
  margin: 0;
  margin-left: 14px;
  font-size: 16px;
  line-height: 18px;
  color: rgb(242, 245, 247);
}
@media (max-width: 1023px) {
  .header__link p {
    display: none;
  }
}

.header__link:hover,
.header__link:focus {
  outline: none;
  box-shadow: 0 0 8px rgb(0, 178, 255);
}
.header__link:hover p,
.header__link:focus p {
  color: rgb(0, 178, 255);
}

.header__link:active {
  outline: none;
  box-shadow: 0 0 8px rgb(0, 178, 255);
}
.header__link:active p {
  color: rgb(5, 71, 99);
}
.header__link:active path {
  fill: rgb(5, 71, 99);
}

.header__link:last-child {
  margin-right: 0;
}
@media (max-width: 1023px) {
  .header__link:last-child {
    margin-right: 0;
  }
}

.header__button {
  align-self: center;
  max-width: 160px;
  margin-left: 17px;
  padding: 9px 15px 13px;
  border: 1px solid rgb(242, 245, 247);
  border-radius: 3px;
  background: transparent;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: rgb(242, 245, 247);
  cursor: pointer;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

.header__button:hover,
.header__button:focus {
  outline: none;
  color: rgb(0, 178, 255);
  border: 1px solid rgb(0, 178, 255);
}

.header__button:active {
  color: rgb(5, 71, 99);
  border: 1px solid rgb(5, 71, 99);
}

.footer {
  display: flex;
  padding: 50px 350px 45px;
  background: #041827;
}
@media (max-width: 1520px) {
  .footer {
    padding: 50px 10px;
    align-items: center;
    justify-content: center;
  }
}
@media (max-width: 1023px) {
  .footer {
    display: block;
    padding: 50px 50px 63px 50px;
  }
}
@media (max-width: 769px) {
  .footer {
    padding: 0;
    padding-bottom: 35px;
  }
}

.footer__wrapper {
  display: flex;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    flex-direction: column;
  }
}

.footer__links-wrapper {
  margin-right: 160px;
}
@media (max-width: 1320px) {
  .footer__links-wrapper {
    margin-right: 80px;
  }
}
@media (max-width: 1023px) {
  .footer__links-wrapper {
    display: flex;
    margin-right: 0;
    margin-bottom: 30px;
  }
}
@media (max-width: 769px) {
  .footer__links-wrapper {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 36px 27px 20px 25px;
  }
}

.footer__logo {
  display: flex;
  margin-bottom: 35px;
}
@media (max-width: 1023px) {
  .footer__logo {
    margin-bottom: 0;
    margin-right: auto;
  }
}
@media (max-width: 769px) {
  .footer__logo {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.socials {
  display: flex;
  margin: 0;
  margin-left: 15px;
  padding: 0;
  gap: 25px;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .socials {
    margin-left: 5px;
  }
}

@media (max-width: 1023px) {
  .socials__item {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.socials__link svg {
  color: rgb(242, 245, 247);
}

.socials__link:hover,
.socials__link:focus {
  outline: none;
}
.socials__link:hover svg,
.socials__link:focus svg {
  color: rgb(0, 178, 255);
}

.socials__link:active svg {
  color: rgb(5, 71, 99);
}

.content {
  margin: 0;
  padding: 0;
  max-width: 250px;
  row-gap: 5px;
  column-gap: 45px;
  display: flex;
  flex-direction: column;
  column-count: 2;
  flex-wrap: wrap;
  height: 140px;
  list-style: none;
}
@media (max-width: 1023px) {
  .content {
    flex-direction: column;
    gap: 5px;
    height: auto;
  }
}
@media (max-width: 769px) {
  .content {
    margin: 0;
    margin-bottom: 15px;
  }
}

.content__item {
  width: 205px;
}

.content__link {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 769px) {
  .content__link {
    font-size: 14px;
  }
}

.content__link:hover,
.content__link:focus {
  outline: none;
  color: rgb(0, 178, 255);
}

.content__link:active {
  color: rgb(5, 71, 99);
}

.contacts {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 7px;
  list-style-type: none;
}

.contacts__link {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 769px) {
  .contacts__link {
    font-size: 14px;
    line-height: 19px;
  }
}

.contacts__link:hover,
.contacts__link:focus {
  outline: none;
  color: rgb(0, 178, 255);
}

.contacts__link:active {
  color: rgb(5, 71, 99);
}

.contacts__item span {
  display: block;
  width: 310px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 769px) {
  .contacts__item span {
    width: 270px;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 19px;
  }
}

.accordion {
  display: flex;
}
@media (max-width: 769px) {
  .accordion {
    flex-direction: column;
  }
}

.accordion__first p,
.accordion__second p {
  position: relative;
  margin: 0;
  margin-top: 8px;
  margin-bottom: 21px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: rgb(255, 255, 255);
}
@media (max-width: 769px) {
  .accordion__first p,
.accordion__second p {
    margin-top: 24px;
    margin-bottom: 18px;
  }
}

.accordion__first p::before,
.accordion__first p::after {
  display: none;
}
@media (max-width: 769px) {
  .accordion__first p::before,
.accordion__first p::after {
    display: block;
    position: absolute;
    top: 25%;
    right: 0;
    content: "";
    width: 15px;
    height: 1px;
    background-color: rgb(242, 245, 247);
  }
}

.accordion__first p::after {
  transform: rotate(90deg);
}

.accordion__second p::before,
.accordion__second p::after {
  display: none;
}
@media (max-width: 769px) {
  .accordion__second p::before,
.accordion__second p::after {
    display: block;
    position: absolute;
    top: 40%;
    right: 0;
    content: "";
    width: 15px;
    height: 1px;
    background-color: rgb(242, 245, 247);
  }
}

.accordion__second p::after {
  transform: rotate(90deg);
}

@media (max-width: 769px) {
  .accordion__second > [data-div=accordion__title].is-close > p::after,
.accordion__first > [data-div=accordion__title].is-close > p::after {
    display: block;
  }
}

@media (max-width: 769px) {
  .accordion__second > [data-div=accordion__title].is-open > p::after,
.accordion__first > [data-div=accordion__title].is-open > p::after {
    display: none;
  }
}

.accordion__first {
  position: relative;
  display: flex;
  flex-flow: column;
  margin-right: 296px;
}
@media (max-width: 1320px) {
  .accordion__first {
    margin-right: 200px;
  }
}
@media (max-width: 1023px) {
  .accordion__first {
    margin-right: 145px;
  }
}
@media (max-width: 769px) {
  .accordion__first {
    margin-right: 0;
    padding: 0 27px 0 25px;
  }
}

@media (max-width: 769px) {
  .accordion__first::before {
    position: absolute;
    left: 0;
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgb(255, 255, 255);
  }
}

@media (max-width: 769px) {
  .accordion__second {
    position: relative;
    padding: 0 27px 14px 25px;
  }
}

@media (max-width: 769px) {
  .accordion__second::before {
    position: absolute;
    left: 0;
    top: 3px;
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgb(255, 255, 255);
  }
}

@media (max-width: 769px) {
  .accordion__second::after {
    position: absolute;
    left: 0;
    bottom: 0;
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgb(255, 255, 255);
  }
}

.accordion__title {
  position: relative;
}

[data-div=accordion__title].is-open ~ [data-ul] {
  display: flex;
}
@media (max-width: 769px) {
  [data-div=accordion__title].is-open ~ [data-ul] {
    display: flex;
  }
}

[data-div=accordion__title].is-close ~ [data-ul] {
  display: flex;
}
@media (max-width: 769px) {
  [data-div=accordion__title].is-close ~ [data-ul] {
    display: none;
  }
}

.main {
  display: flex;
  flex-direction: column;
}

.main__main-screen {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
@media (max-width: 1023px) {
  .main__main-screen {
    align-items: flex-start;
    justify-content: flex-start;
  }
}

.main__main-screen::before {
  position: absolute;
  top: -25px;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%);
}

.main-screen__image {
  position: absolute;
  z-index: -1;
  top: -22px;
  left: 0;
  width: 100%;
  height: 100%;
}
@media (max-width: 1023px) {
  .main-screen__image {
    top: -78px;
  }
}
@media (max-width: 769px) {
  .main-screen__image {
