.advantages {
  display: flex;
  align-items: center;
  justify-content: center;
}

.advantages__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 30px;
  margin: 0;
  padding: 97px 40px 90px;
  list-style-type: none;
}
@media (max-width: 1023px) {
